	module.exports = {"data":{"markdownRemark":{"html":"<h2>Troubleshooting with Rails.logger.info</h2>\n<p>This short post will be discussing the magic of the Rails error logging. I literally learned about it today and literally have zero blog readers so I don't feel too bad with the misinformation campaign below.</p>\n<p>Assume you have an error you don't understand. An error that doesn't even necessarily cause your view to show an error message. Maybe you are spitting out JSON which is not quite as verbose about things not working as, say, an error that would mess up an HTML-based view. You need some help. You need to know what is in those variables. You need Rails logger!</p>\n<h3>Rails.logger.info</h3>\n<p>Putting in Rails.logger.info <em>some_var</em> into your Ruby code will output that <em>some_var</em> to your test log (see next section). You can output strings enclosed in quotes, variables as variables, variables using string interpolation for more readable error logging, or dive into variables with <em>some_var.inspect</em>.</p>\n<p>So for example, you can write Rails.logger.info @current_user.inspect to see the status of a currently logged in user while running, say, RSpec or playing with a live app on your local machine.</p>\n<h3>iTerm Use Case</h3>\n<p>In iTerm (or Terminal, or other command line du jure) you can type in</p>\n<blockquote>\n<blockquote>\n<blockquote>\n<p>$ tail -f log/test.log</p>\n<p>and now view an activity log including your Rails.logger.info entries to pick apart what is going on in your code.</p>\n</blockquote>\n</blockquote>\n</blockquote>\n<h3>Helpful hints</h3>\n<p>I can only pass on the three I know, so here goes.</p>\n<ol>\n<li>Be sure to delete out your Rails.logger.info code once you are done.</li>\n<li>Don't be shy about using lines of text so provide visual ID'ing of your tests. It is hard to find the inserted logger lines so \"!!!!!!!!!!!!!\" is not a bad idea.</li>\n<li>You can only pass one variable per line with Rails.logger.info</li>\n</ol>","frontmatter":{"date":"December 11, 2015","path":"/blog/troubleshooting-with-rails/","title":"Troubleshooting with Rails","type":"blog"}}},"pathContext":{}}

